export const isAuth = () => {
    if (localStorage.getItem('isAuth') === 'true') {
        return true
    } else {
        return false
    }
};

export const logIn = () => {
    localStorage.setItem('isAuth', true);
};

export const logOff = () => {
    localStorage.setItem('isAuth', false);
};

export const registrationNewUser = () => {
    // –≤ –¥–∞–Ω–Ω—ã–π –º–æ–º–µ–Ω—Ç —Ñ—É–Ω–∫—Ü–∏—è –Ω–µ —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω–∞
    localStorage.setItem('isAuth', true);
};



